import classNames from 'classnames'
import * as React from 'react'
import { useContext, useEffect, useState } from 'react'
import { heroHeadStyleResource } from '../../Resources/GameResource'
import { HeroContext } from '../../utils/context'

interface IHeroGameViewProps {}

const HeroGameView: React.FunctionComponent<IHeroGameViewProps> = (props) => {
  const { heroHeadId } = useContext(HeroContext)
  const [isAtk, setIsAtk] = useState<boolean>(false)
  const [isHealing, setIsHealing] = useState<boolean>(false)
  const [isEvading, setIsEvading] = useState<boolean>(false)

  const handleMessage = (event: MessageEvent) => {
    if (event.data.type === 'hero') {
      if (event.data.payload.atk !== undefined) setIsAtk(event.data.payload.atk)
      if (event.data.payload.heal !== undefined)
        setIsHealing(event.data.payload.heal)
      if (event.data.payload.evade !== undefined)
        setIsEvading(event.data.payload.evade)
    }
  }

  useEffect(() => {
    window.addEventListener('message', handleMessage, false)
    return () => {
      window.removeEventListener('message', handleMessage)
    }
  })

  return (
    <div>
      <div className="flex justify-center">You</div>
      <div className="p-6 border border-black bg-white">
        <div
          className={classNames(
            'hover:text-blue-600 hover:cursor-pointer leading-none px-2.5 py-1 ',
            {
              'animate-right-atk': isAtk,
              'animate-heal': isHealing,
              'animate-evade-left': isEvading
            }
          )}
        >
          {heroHeadStyleResource[heroHeadId]}
        </div>
      </div>
    </div>
  )
}

export default HeroGameView
