import * as React from 'react'
import { useContext, useEffect } from 'react'
import { HeroContext } from '../utils/context'
import { calculateExtraHpFromVitality } from '../utils/utils'

interface IHeroStatUpdateWrapperProps {}

const HeroStatUpdateWrapper: React.FunctionComponent<
  IHeroStatUpdateWrapperProps
> = (props) => {
  const { heroLevel, setHeroMaxHp, heroHp, heroMaxHp, setHeroHp } =
    useContext(HeroContext)

  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      if (event.data.type === 'stat') {
        const { payload } = event.data
        Object.keys(event.data.payload).forEach((stat: string) => {
          switch (stat) {
            case 'vitality':
              const hp = calculateExtraHpFromVitality(heroLevel, payload[stat])
              if (heroHp === heroMaxHp) {
                setHeroHp(heroMaxHp + hp)
              }
              setHeroMaxHp(heroMaxHp + hp)
              break
            default:
              break
          }
        })
      }
    }

    window.addEventListener('message', handleMessage, false)
    return () => {
      window.removeEventListener('message', handleMessage)
    }
  })

  return null
}

export default HeroStatUpdateWrapper
