import * as React from 'react'
import { useContext } from 'react'
import { PlayerStateContext } from '../utils/context'
import { EEnemyState, EPlayerAction, IEnemy } from '../utils/interfaces'
import EnemyGameView from './View/EnemyGameView'

interface IEnemyWrapperProps {}

const EnemyWrapper: React.FunctionComponent<IEnemyWrapperProps> = (props) => {
  const {
    isWaitingForEnemySelection,
    setIsWaitingForEnemySelection,
    setEnemySelected,
    playerAction
  } = useContext(PlayerStateContext)

  const handleOnEnemyClick = (enemy: IEnemy) => {
    if (
      enemy.enemyState !== EEnemyState.dead &&
      isWaitingForEnemySelection &&
      playerAction === EPlayerAction.attack
    ) {
      setEnemySelected(enemy)
      setIsWaitingForEnemySelection(false)
    }
  }
  return <EnemyGameView onEnemyClick={handleOnEnemyClick} />
}

export default EnemyWrapper
