import React, { useState } from 'react'
import './App.css'
import GameScreenWrapper from './GameScreen/GameScreenWrapper'
import StartScreen from './StartScreen/StartScreen'
import {
  ActionLogsContext,
  EnemiesContext,
  EnemyHoverContext,
  GameContext,
  HeroContext,
  PlayerStateContext
} from './utils/context'
import {
  defaultActionLogs,
  defaultEnemies,
  defaultEnemy,
  defaultGameState,
  defaultHero,
  defaultPlayerState
} from './utils/defaultData'
import {
  EEntity,
  EGameState,
  ERoomState,
  IActionLog,
  IEnemy,
  IEntityTurn,
  IEntityTurns,
  IITems,
  IPlayerAction,
  IStats
} from './utils/interfaces'

interface IAppProps {}

const App: React.FunctionComponent<IAppProps> = (props) => {
  // Game state
  const [gState, setGState] = useState<EGameState>(defaultGameState.gState)
  const [entityTurn, setEntityTurn] = useState<IEntityTurn>(
    defaultGameState.entityTurn
  )
  const [entityTurns, setEntityTurns] = useState<IEntityTurns>(
    defaultGameState.entityTurns
  )
  const [roomState, setRoomState] = useState<ERoomState>(
    defaultGameState.roomState
  )
  const [roomNumber, setRoomNumber] = useState<number>(
    defaultGameState.roomNumber
  )

  // Hero state
  const [heroName, setHeroName] = useState<string>(defaultHero.heroName)
  const [heroXp, setHeroXp] = useState<number>(defaultHero.heroXp)
  const [heroHp, setHeroHp] = useState<number>(defaultHero.heroHp)
  const [heroMaxHp, setHeroMaxHp] = useState<number>(defaultHero.heroHp)
  const [heroLevel, setHeroLevel] = useState<number>(defaultHero.heroLevel)
  const [heroHeadId, setHeroHeadId] = useState<number>(defaultHero.heroHeadId)
  const [heroItems, setHeroItems] = useState<IITems[]>(defaultHero.heroItems)
  const [heroEntity, setHeroEntity] = useState<EEntity>(defaultHero.heroEntity)
  const [heroSkillPoints, setHeroSkillPoints] = useState<number>(
    defaultHero.heroSkillPoints
  )
  const [heroStats, setHeroStats] = useState<IStats>(defaultHero.heroStats)

  const [enemies, setEnemies] = useState<IEnemy[]>(defaultEnemies.enemies)

  const [enemyHoverId, setEnemyHoverId] = useState<number>(defaultEnemy.enemyId)

  const [actionLogs, setActionLogs] = useState<IActionLog[]>(
    defaultActionLogs.actionLogs
  )

  const [playerAction, setPlayerAction] = useState<IPlayerAction>(
    defaultPlayerState.playerAction
  )
  const [isWaitingForEnemySelection, setIsWaitingForEnemySelection] =
    useState<boolean>(defaultPlayerState.isWaitingForEnemySelection)
  const [enemySelected, setEnemySelected] = useState<IEnemy | undefined>(
    defaultPlayerState.enemySelected
  )

  return (
    <div className="p-10">
      <PlayerStateContext.Provider
        value={{
          playerAction,
          setPlayerAction,
          isWaitingForEnemySelection,
          setIsWaitingForEnemySelection,
          enemySelected,
          setEnemySelected
        }}
      >
        <ActionLogsContext.Provider value={{ actionLogs, setActionLogs }}>
          <GameContext.Provider
            value={{
              gState,
              setGState,
              entityTurn,
              setEntityTurn,
              entityTurns,
              setEntityTurns,
              roomState,
              setRoomState,
              roomNumber,
              setRoomNumber
            }}
          >
            <EnemiesContext.Provider value={{ enemies, setEnemies }}>
              <EnemyHoverContext.Provider
                value={{ enemyHoverId, setEnemyHoverId }}
              >
                <HeroContext.Provider
                  value={{
                    heroName,
                    setHeroName,
                    heroXp,
                    setHeroXp,
                    heroHp,
                    setHeroHp,
                    heroLevel,
                    setHeroLevel,
                    heroHeadId,
                    setHeroHeadId,
                    heroEntity,
                    setHeroEntity,
                    heroItems,
                    setHeroItems,
                    heroSkillPoints,
                    setHeroSkillPoints,
                    heroStats,
                    setHeroStats,
                    heroMaxHp,
                    setHeroMaxHp
                  }}
                >
                  {gState === EGameState.startScreen ? (
                    <StartScreen />
                  ) : (
                    <GameScreenWrapper />
                  )}
                </HeroContext.Provider>
              </EnemyHoverContext.Provider>
            </EnemiesContext.Provider>
          </GameContext.Provider>
        </ActionLogsContext.Provider>
      </PlayerStateContext.Provider>
    </div>
  )
}

export default App
