import * as React from 'react'
import { useContext, useEffect } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import { enemiesGenerator } from '../utils/roomGenerator'
import { generateGameTurn } from '../utils/turnBasedGenerator'
import {
  ActionLogsContext,
  EnemiesContext,
  GameContext,
  HeroContext
} from '../utils/context'
import { EGameState, IEntityTurns, EActionLogType } from '../utils/interfaces'

interface IInitGameWrapperProps {}

const InitGameWrapper: React.FunctionComponent<IInitGameWrapperProps> = (
  props
) => {
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const { enemies, setEnemies } = useContext(EnemiesContext)
  const { gState, setGState, setEntityTurn, setEntityTurns } =
    useContext(GameContext)
  const { heroEntity, heroLevel } = useContext(HeroContext)

  useEffect(() => {
    if (gState === EGameState.notInitialised) {
      const enemies = enemiesGenerator(heroLevel)
      setEnemies(enemies)
      const turn: IEntityTurns = generateGameTurn({ heroEntity }, enemies)
      setEntityTurns(turn)
      setEntityTurn(turn[0])
      setGState(EGameState.readyToStart)
      const newLogs = [
        ...actionLogs,
        formatNewLog(EActionLogType.system, 'List of foes has been generated'),
        formatNewLog(
          EActionLogType.system,
          'Initial turn order has been generated'
        ),
        formatNewLog(EActionLogType.system, 'Game state is "Ready to start"')
      ]
      setActionLogs(newLogs)
    }
  }, [
    actionLogs,
    enemies,
    gState,
    heroEntity,
    heroLevel,
    setActionLogs,
    setEnemies,
    setEntityTurn,
    setEntityTurns,
    setGState
  ])
  return null
}

export default InitGameWrapper
