import * as React from 'react'
import { useContext } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import { ActionLogsContext, HeroContext } from '../utils/context'
import {
  EActionLogType,
  EItemType,
  IItemResource,
  IITems
} from '../utils/interfaces'
import { sendMessage } from '../utils/utils'
import { consumeHealPotion } from './inventoryUtils'
import InventoryView from './View/InventoryView'

interface IInventoryWrapperProps {}

const InventoryWrapper: React.FunctionComponent<IInventoryWrapperProps> = (
  props
) => {
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const { heroHp, setHeroHp, heroItems, setHeroItems, heroMaxHp } =
    useContext(HeroContext)

  const updateHeroItemList = (item: IITems) => {
    item.amount -= 1
    heroItems.forEach((item: IITems, idx: number) => {
      if (item.amount === 0) {
        heroItems.splice(idx, 1)
      }
    })
    setHeroItems(heroItems)
  }

  const handleItemUseClick = (item: IITems) => {
    let logMsg = ''

    switch (item.type) {
      case EItemType.consumable:
        consumeHealPotion(
          heroHp,
          setHeroHp,
          heroMaxHp,
          (item.itemResource as IItemResource & { value: number }).value
        )
        logMsg = `Player used ${item.itemResource.label} and healed ${
          (item.itemResource as IItemResource & { value: number }).value
        } hp`
        sendMessage('hero', { heal: true })
        sendMessage('hero', { heal: false }, 500)
        break
      default:
        break
    }

    setActionLogs([
      ...actionLogs,
      formatNewLog(EActionLogType.playerAction, logMsg)
    ])
    updateHeroItemList(item)
  }
  return <InventoryView itemUseClick={handleItemUseClick} />
}

export default InventoryWrapper
