import * as React from 'react'
import { useContext } from 'react'
import { EnemiesContext, GameContext, HeroContext } from '../utils/context'
import { EGameState } from '../utils/interfaces'
import ActionLogPanel from '../Logs/ActionLogsPanel'
import { isAnyEnemyAlive, isPlayerAlive } from './gameScreenUtils'
import RoomEnding from './RoomEndingWrapper'
import DeathScreen from './DeathScreenWrapper'
import InventoryWrapper from '../Inventory/InventoryWrapper'
import ActionWrapper from '../Actions/ActionWrapper'
import EnemyWrapper from '../Enemy/EnemyWrapper'
import HeroWrapper from '../Hero/HeroWrapper'
import HeroInfoWrapper from '../Hero/HeroInfoWrapper'
import InitGameWrapper from './InitGameWrapper'
import EnemyActionWrapper from '../Enemy/EnemyActionsWrapper'
import InfoHoverView from '../InfoHover/InfoHoverView'
import GameLogic from '../GameLogic/GameLogic'
import PopupRoomLoot from './PopupRoomLoots'

interface IGameScreenWrapperProps {}

const GameScreenWrapper: React.FunctionComponent<IGameScreenWrapperProps> = (
  props
) => {
  const { enemies } = useContext(EnemiesContext)
  const { gState } = useContext(GameContext)
  const { heroHp } = useContext(HeroContext)

  return (
    <div>
      <InitGameWrapper />
      {gState === EGameState.readyToStart && (
        <>
          <div className="mb-6">
            <HeroInfoWrapper />
          </div>
          <div className="flex flex-col md:flex-row">
            <div className="mr-10 mb-10 self-center">
              <HeroWrapper />
            </div>
            <div>
              <EnemyWrapper />
            </div>
          </div>
          <div>
            {!isPlayerAlive(heroHp) ? (
              <DeathScreen />
            ) : (
              <>
                {isAnyEnemyAlive(enemies) ? <ActionWrapper /> : <RoomEnding />}
              </>
            )}
          </div>
          <div>
            <InventoryWrapper />
          </div>
          <div>
            <InfoHoverView />
          </div>
          <div>
            <ActionLogPanel />
          </div>

          <EnemyActionWrapper />
          {/* <RoomWrapper /> */}
          <PopupRoomLoot />
          <GameLogic />
        </>
      )}
    </div>
  )
}

export default GameScreenWrapper

