import * as React from 'react'
import { useContext, useEffect } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import { generateGameTurn } from '../utils/turnBasedGenerator'
import { enemiesResource } from '../Resources/GameResource'
import { FOE_ATTACK_TIMER } from '../utils/constants'
import {
  ActionLogsContext,
  EnemiesContext,
  GameContext,
  HeroContext
} from '../utils/context'
import {
  IEntityTurns,
  EEnemyState,
  EActionLogType,
  EGameState,
  EEntity
} from '../utils/interfaces'
import {
  calculateDefenseMitigation,
  calculateDodgeChance,
  getDamage,
  isEvading,
  sendMessage
} from '../utils/utils'
import { isPlayerAlive } from '../GameScreen/gameScreenUtils'

interface IEnemyActionWrapperProps {}

const EnemyActionWrapper: React.FunctionComponent<IEnemyActionWrapperProps> = (
  props
) => {
  const { heroEntity, heroHp, setHeroHp, heroStats } = useContext(HeroContext)
  const { gState, setEntityTurn, setEntityTurns, entityTurn, entityTurns } =
    useContext(GameContext)
  const { enemies } = useContext(EnemiesContext)
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)

  useEffect(() => {
    let timer: NodeJS.Timeout
    const setNewEntityTurns = () => {
      const turn: IEntityTurns = generateGameTurn({ heroEntity }, enemies)
      setEntityTurns(turn)
      setEntityTurn(turn[0])
    }
    const setNextTurn = () => {
      entityTurns.shift()
      if (entityTurns.length > 0) {
        setEntityTurns([...entityTurns])
        setEntityTurn(entityTurns[0])
      } else {
        setNewEntityTurns()
      }
    }

    const automateFoeAction = () => {
      timer = setTimeout(() => {
        if (
          enemies[entityTurn.id].enemyState === EEnemyState.alive &&
          isPlayerAlive(heroHp)
        ) {
          const evading = isEvading(
            calculateDodgeChance(heroStats.dexterity) / 100
          )
          if (!evading) {
            const currentEnemy = enemies[entityTurn.id]
            const dmg = getDamage(
              currentEnemy.enemyLevel,
              currentEnemy.enemyStats.strength
            )
            const dmgReduction = calculateDefenseMitigation(
              heroStats.robustness
            )
            const mitigatedDmg = Math.round(dmg * (dmgReduction / 100))
            const updatedDmg = dmg - mitigatedDmg
            const newHeroHp = heroHp - updatedDmg

            if (newHeroHp <= 0) {
              setHeroHp(0)
            } else {
              setHeroHp(newHeroHp)
            }
            setActionLogs([
              ...actionLogs,
              formatNewLog(
                EActionLogType.enemyAction,
                `${
                  enemiesResource[
                    enemies.find((enemy) => enemy.enemyId === entityTurn.id)
                      ?.enemyType || 0
                  ].name
                } is attacking player for ${updatedDmg} damage (mitigated ${mitigatedDmg} damage)`
              ),
              ...(!isPlayerAlive(newHeroHp)
                ? [
                    formatNewLog(
                      EActionLogType.playerAction,
                      `Player died from ${
                        enemiesResource[
                          enemies.find(
                            (enemy) => enemy.enemyId === entityTurn.id
                          )?.enemyType || 0
                        ].name
                      }`
                    )
                  ]
                : [])
            ])
          } else {
            sendMessage('hero', { evade: true })
            sendMessage('hero', { evade: false }, 1000)

            setActionLogs([
              ...actionLogs,
              formatNewLog(
                EActionLogType.enemyAction,
                `${
                  enemiesResource[
                    enemies.find((enemy) => enemy.enemyId === entityTurn.id)
                      ?.enemyType || 0
                  ].name
                } is attacking player`
              ),
              formatNewLog(
                EActionLogType.playerAction,
                'Player just evade the attack'
              )
            ])
          }
          if (entityTurns.length > 0) {
            setNextTurn()
          } else {
            formatNewLog(
              EActionLogType.system,
              'EntityTurns is empty, generate new list'
            )
            setNewEntityTurns()
          }
        } else {
          setNextTurn()
        }
      }, FOE_ATTACK_TIMER)
    }

    if (gState === EGameState.readyToStart) {
      if (entityTurn.entity !== EEntity.player) {
        if (enemies[entityTurn.id].enemyState === EEnemyState.alive) {
          automateFoeAction()
        } else {
          setNextTurn()
        }
      }
    }
    return () => clearTimeout(timer)
  }, [
    actionLogs,
    enemies,
    entityTurn,
    entityTurns,
    gState,
    heroEntity,
    heroHp,
    heroStats.dexterity,
    heroStats.robustness,
    setActionLogs,
    setEntityTurn,
    setEntityTurns,
    setHeroHp
  ])
  return null
}

export default EnemyActionWrapper
