import { itemsResource } from '../Resources/GameResource'
import {
  FACT_CONSTANT_DAMAGE,
  MAX_DMG_FACT,
  MIN_DMG_FACT,
  BASE_DAMAGE,
  PLAYER_BASE_SKILL_POINT,
  PLAYER_CONSTANT_SKILL_POINT,
  XP_CONSTANT,
  VITALITY_CONSTANT_HP,
  VITALITY_BASE_HP,
  EVADE_PERCENT_BASE,
  DEFENSE_PERCENT_BASE
} from './constants'
import { IITems } from './interfaces'

const average = (arr: number[]) => arr.reduce((a, b) => a + b, 0) / arr.length

export const rand = (max: number): number => Math.floor(Math.random() * max)

export const randMinMax = (min: number, max: number) =>
  Math.random() * (max - min) + min

export const xpToNextLevel = (level: number) => {
  const xpToNext =
    ((Math.pow(level + 1, 2) + level + 1) / 2) * XP_CONSTANT -
    (level + 1) * XP_CONSTANT
  return Math.floor(xpToNext - xpForCurrentLevel(level))
}

export const xpForCurrentLevel = (level: number) =>
  Math.floor(
    ((Math.pow(level, 2) + level) / 2) * XP_CONSTANT - level * XP_CONSTANT
  )

export const calculateXpDrop = (level: number, base_xp: number) => {
  return Math.round(level * base_xp + base_xp)
}

export const compareEnemyId = (a: any, b: any) => {
  if (a.enemyId < b.enemyId) {
    return -1
  }
  if (a.enemyId > b.enemyId) {
    return 1
  }
  return 0
}

export const getItemIdByName = (name: string) =>
  Object.keys(itemsResource).find(
    (key: string) => itemsResource[key].label === name
  )

export const calculateHeroDmg = (level: number) => {
  return (
    Math.round(level * FACT_CONSTANT_DAMAGE + BASE_DAMAGE) +
    rand(Math.round(level * FACT_CONSTANT_DAMAGE + BASE_DAMAGE) / 1.5)
  )
}

export const calculateHp = (
  level: number,
  vitality: number,
  base_hp: number,
  base_health: number
) => {
  return Math.round(level * (base_hp * vitality) + base_health)
}

export const calculateHpOnLevelUp = (hp: number) =>
  Math.round((hp += hp * 0.03))

export const calculateExtraHpFromVitality = (
  level: number,
  vitality: number
) => {
  return Math.round(
    level * (VITALITY_CONSTANT_HP * vitality) + VITALITY_BASE_HP
  )
}

export const sendMessage = (type: string, payload: {}, delay: number = 0) => {
  setTimeout(() => {
    window.postMessage(
      {
        type,
        payload
      },
      '*'
    )
  }, delay)
}

export const calculateDodgeChance = (dexterity: number) => {
  return dexterity * EVADE_PERCENT_BASE
}

export const calculateDefenseMitigation = (robustness: number) => {
  return robustness * DEFENSE_PERCENT_BASE
}

export const calculateSkillPoints = (level: number) => {
  return Math.round(
    level * PLAYER_CONSTANT_SKILL_POINT + PLAYER_BASE_SKILL_POINT
  )
}

export const getBaseDmg = (level: number, strength: number) =>
  Math.round(level * (FACT_CONSTANT_DAMAGE * strength) + BASE_DAMAGE)

const calculateFactDmg = (baseDmg: number) =>
  Math.round(
    randMinMax(
      baseDmg - (baseDmg / 100) * MIN_DMG_FACT,
      baseDmg + (baseDmg / 100) * MAX_DMG_FACT
    )
  )

const getAverageFactDmg = (baseDmg: number, amount: number = 10) => {
  const dataDmg = []
  for (let i = 0; i < amount; i++) {
    dataDmg.push(calculateFactDmg(baseDmg))
  }
  return Math.round(average(dataDmg))
}

export const getDamage = (level: number, strength: number) =>
  getAverageFactDmg(getBaseDmg(level, strength))

export const isEvading = (evadePercent: number) => {
  const chance = Math.random()
  return chance < evadePercent + 0.05
}

export const mergeItemsArray = (loots: IITems[]) => {
  const res = loots.reduce((prev: IITems[], curr: IITems) => {
    let item = prev.find((prevItem: IITems) => prevItem.key === curr.key)
    if (!item) {
      prev.push(Object.assign({}, curr))
    } else {
      item.amount += curr.amount
    }
    return prev
  }, [])
  return res
}
