import { createContext } from 'react'
import {
  defaultActionLogs,
  defaultEnemies,
  defaultEnemyHover,
  defaultGameState,
  defaultHero,
  defaultPlayerState
} from './defaultData'
import {
  IActionLogs,
  IEnemies,
  IEnemyHover,
  IGameState,
  IHero,
  IPlayerState
} from './interfaces'

export const HeroContext = createContext<IHero>(defaultHero)

export const GameContext = createContext<IGameState>(defaultGameState)

export const EnemiesContext = createContext<IEnemies>(defaultEnemies)

export const EnemyHoverContext = createContext<IEnemyHover>(defaultEnemyHover)

export const ActionLogsContext = createContext<IActionLogs>(defaultActionLogs)

export const PlayerStateContext =
  createContext<IPlayerState>(defaultPlayerState)
