import * as React from 'react'
import { useContext, useState } from 'react'
import { itemsResource } from '../../Resources/GameResource'
import { HeroContext } from '../../utils/context'
import { IITems } from '../../utils/interfaces'

interface IInventoryViewProps {
  itemUseClick: (item: IITems) => void
}

const InventoryView: React.FunctionComponent<IInventoryViewProps> = ({
  itemUseClick
}) => {
  const { heroItems } = useContext(HeroContext)
  const [toggleInventory, setToggleInventory] = useState<boolean>(false)

  return (
    <div className="my-6">
      <div className="flex mb-6">
        <div className="mr-6">Inventory</div>
        <div>
          <button
            className="border border-gray-600 px-4 bg-white active:bg-gray-200 disabled:text-gray-400 disabled:border-gray-400"
            type="button"
            onClick={() => setToggleInventory(!toggleInventory)}
          >
            {!toggleInventory ? 'Show' : 'Hide'}
          </button>
        </div>
      </div>
      {toggleInventory && (
        <div className="flex flex-wrap">
          <>
            {heroItems.length > 0 ? (
              heroItems.map((item: IITems, idx: number) => {
                return (
                  <div
                    key={idx}
                    className="ring-1 ring-inset ring-black border-b-transparent w-20 h-20 mr-4 relative mb-10 bg-white"
                  >
                    <div className="h-full flex justify-center relative">
                      <div className="text-center text-sm self-center">
                        {/* {itemsResource[item.type]?.label || 'mocked Item'} */}
                        {item.itemResource.label}
                      </div>
                      <div className="absolute bottom-0.5 text-xs">
                        {'x ' + item.amount}
                      </div>
                    </div>
                    {item.itemResource?.isConsumable && (
                      <div className="">
                        <button
                          className="absolute left-0 bottom-[-25px] border border-gray-600 px-4 active:bg-gray-200 disabled:text-gray-400 disabled:border-gray-400 w-full bg-white"
                          type="button"
                          onClick={() => itemUseClick(item)}
                        >
                          Use
                        </button>
                      </div>
                    )}
                  </div>
                )
              })
            ) : (
              <div>You have nothing in your inventory</div>
            )}
          </>
        </div>
      )}
    </div>
  )
}

export default InventoryView
