import classNames from 'classnames'
import * as React from 'react'
import { useContext, useEffect, useState } from 'react'
import { enemiesResource } from '../../Resources/GameResource'
import {
  EnemiesContext,
  EnemyHoverContext,
  GameContext,
  PlayerStateContext
} from '../../utils/context'
import { EEnemyState, IEnemy } from '../../utils/interfaces'

interface IEnemyGameViewProps {
  onEnemyClick: (arg: IEnemy) => void
}

const EnemyGameView: React.FunctionComponent<IEnemyGameViewProps> = ({
  onEnemyClick
}) => {
  const { enemies } = useContext(EnemiesContext)
  const { setEnemyHoverId, enemyHoverId } = useContext(EnemyHoverContext)
  const { entityTurn } = useContext(GameContext)
  const { isWaitingForEnemySelection } = useContext(PlayerStateContext)
  const [isEvading, setIsEvading] = useState<{ id: number; evade: boolean }>()

  const handleMessage = (event: MessageEvent) => {
    if (event.data.type === 'enemy') {
      if (event.data.payload.evade !== undefined)
        setIsEvading({
          id: event.data.payload.id,
          evade: event.data.payload.evade
        })
    }
  }

  useEffect(() => {
    window.addEventListener('message', handleMessage, false)
    return () => {
      window.removeEventListener('message', handleMessage)
    }
  })

  return (
    <div>
      <div className="flex justify-center">Foes</div>
      <div className="border border-black bg-white p-6">
        <div className="flex flex-wrap relative">
          {enemies.map((enemy: IEnemy, idx: number) => {
            return (
              <div
                key={idx}
                title={enemiesResource[enemy.enemyType].style}
                className={classNames(
                  'hover:cursor-pointer leading-none w-8 h-8 mr-1 flex justify-center animation-delay-700',
                  {
                    'italic text-gray-400':
                      enemy.enemyState === EEnemyState.dead,
                    'animate-left-atk': entityTurn.id === enemy.enemyId,
                    'border-2 border-red-600 animate-target-enemy hover:bg-red-600 hover:text-black ':
                      isWaitingForEnemySelection &&
                      enemy.enemyState !== EEnemyState.dead,
                    'font-bold': enemy.enemyId === enemyHoverId,
                    'animate-evade-right animation-delay-150':
                      isEvading &&
                      isEvading.evade &&
                      isEvading.id === enemy.enemyId
                  }
                )}
                data-id={enemy.enemyId}
                onMouseEnter={() => setEnemyHoverId(enemy.enemyId)}
                // onMouseLeave={() => setEnemyHoverId(-1)}
                onClick={() => onEnemyClick(enemy)}
              >
                <div className="self-center">
                  {enemiesResource[enemy.enemyType].style}
                </div>
              </div>
            )
          })}
        </div>
      </div>
      {/* {isWaitingForEnemySelection && <div>Select the foe to attack</div>} */}
    </div>
  )
}

export default EnemyGameView
