import * as React from 'react'
import { useContext, useEffect, useState } from 'react'
import { showStatData } from '../Hero/View/HeroStatsView'
import { enemiesResource } from '../Resources/GameResource'
import { EnemiesContext, EnemyHoverContext } from '../utils/context'
import { defaultEnemy } from '../utils/defaultData'
import { IEnemy, IStats } from '../utils/interfaces'

interface IInfoHoverViewProps {}

const InfoHoverView: React.FunctionComponent<IInfoHoverViewProps> = (props) => {
  const { enemyHoverId } = useContext(EnemyHoverContext)
  const { enemies } = useContext(EnemiesContext)
  const [enemyInfo, setEnemyInfo] = useState<IEnemy>(defaultEnemy)

  useEffect(() => {
    const getEnemyById = (id: number) =>
      enemies.find((enemy: IEnemy) => enemy.enemyId === id)
    const enemy = getEnemyById(enemyHoverId)
    if (enemy) {
      setEnemyInfo(enemy)
    }
  }, [enemies, enemyHoverId])

  return (
    <div className="mt-6">
      {enemyHoverId !== -1 && enemyInfo.enemyId !== -1 && (
        <div>
          <div>Info</div>
          <div className="flex">
            <div className="p-6 border border-black bg-white">
              <div className="hover:text-blue-600 cursor-pointer select-none">
                <div>Type: {enemiesResource[enemyInfo?.enemyType].name}</div>
                <div>Level: {enemyInfo?.enemyLevel}</div>
                <div className="mb-2">
                  HP: {`${enemyInfo?.enemyHp} / ${enemyInfo?.enemyMaxHp}`}
                </div>
                <div>
                  {Object.keys(enemyInfo.enemyStats).map(
                    (key: string, idx: number) => (
                      <div className="flex" key={idx}>
                        <div className="flex-1 min-w-[112px]">{`${
                          key as string
                        } : ${enemyInfo.enemyStats[key as keyof IStats]}`}</div>

                        {showStatData(
                          enemyInfo.enemyLevel,
                          key as keyof IStats,
                          enemyInfo.enemyStats
                        )}
                      </div>
                    )
                  )}
                </div>
              </div>
            </div>
            <div className="flex-1"></div>
          </div>
        </div>
      )}
    </div>
  )
}

export default InfoHoverView
