export enum EGameState {
  startScreen,
  notInitialised,
  initialised,
  readyToStart,
  inProgress,
  finished
}

export enum ERoomState {
  notReady,
  readyToStart,
  onGoing,
  finished
}

export enum EEntity {
  player,
  foe
}

export enum EEnemyState {
  alive,
  dead
}

export enum EPlayerAction {
  attack,
  heal,
  defend,
  skip,
  none
}

export enum EItemType {
  weapon,
  currency,
  consumable,
  none
}

export interface IStats {
  strength: number
  dexterity: number
  vitality: number
  robustness: number
}

export type IPlayerAction = EPlayerAction
export interface IPlayerState {
  isWaitingForEnemySelection: boolean
  setIsWaitingForEnemySelection: (arg: boolean) => void
  enemySelected: IEnemy | undefined
  setEnemySelected: (arg: IEnemy | undefined) => void
  playerAction: IPlayerAction
  setPlayerAction: (arg: IPlayerAction) => void
}

export interface IGameState {
  gState: EGameState
  setGState: (arg: EGameState) => void
  entityTurns: IEntityTurns
  setEntityTurns: (arg: IEntityTurn[]) => void
  entityTurn: IEntityTurn
  setEntityTurn: (arg: IEntityTurn) => void
  roomState: ERoomState
  setRoomState: (arg: ERoomState) => void
  roomNumber: number
  setRoomNumber: (arg: number) => void
}

export interface IHero {
  heroName: string
  setHeroName: (arg: string) => void
  heroHp: number
  setHeroHp: (arg: number) => void
  heroMaxHp: number
  setHeroMaxHp: (arg: number) => void
  heroXp: number
  setHeroXp: (arg: number) => void
  heroLevel: number
  setHeroLevel: (arg: number) => void
  heroHeadId: number
  setHeroHeadId: (arg: number) => void
  heroItems: IITems[]
  setHeroItems: (arg: IITems[]) => void
  heroEntity: EEntity
  setHeroEntity: (arg: EEntity) => void
  heroSkillPoints: number
  setHeroSkillPoints: (arg: number) => void
  heroStats: IStats
  setHeroStats: (arg: IStats) => void
}

export interface IEnemies {
  enemies: IEnemy[]
  setEnemies: (arg: IEnemy[]) => void
}
export interface IEnemy {
  enemyId: number
  setEnemyId: (arg: number) => void
  enemyType: number
  setEnemyType: (arg: number) => void
  enemyLevel: number
  setEnemyLevel: (arg: number) => void
  enemyHp: number
  setEnemyHp: (arg: number) => void
  enemyMaxHp: number
  setEnemyMaxHp: (arg: number) => void
  enemyEntity: EEntity
  setEnemyEntity: (arg: EEntity) => void
  enemyState: EEnemyState
  setEnemyState: (arg: EEnemyState) => void
  enemyLoots: IITems[]
  setEnemyLoots: (arg: IITems[]) => void
  enemyStats: IStats
  setEnemyStats: (arg: IStats) => void
}

export interface IEnemyHover {
  enemyHoverId: number
  setEnemyHoverId: (arg: number) => void
}

export interface IITems {
  type: EItemType
  key: string
  amount: number
  itemResource: IItemResource
}

export type IEntityTurns = IEntityTurn[]
export type IEntityTurn = { id: number; entity: EEntity }

export enum EActionLogType {
  playerAction,
  enemyAction,
  system,
  other
}
export interface IActionLog {
  type: EActionLogType
  timestamp: string
  message: string
}

export interface IActionLogs {
  actionLogs: IActionLog[]
  setActionLogs: (arg: IActionLog[]) => void
}

export interface IItemResource {
  label: string
  isConsumable?: boolean
  dropRate: { min: number; max: number }
  stats?: IStats
  type: EItemType
}
