import * as React from 'react'
import { useContext, useEffect } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import { ActionLogsContext, HeroContext } from '../utils/context'
import { EActionLogType } from '../utils/interfaces'
import {
  calculateHpOnLevelUp,
  calculateSkillPoints,
  sendMessage,
  xpToNextLevel
} from '../utils/utils'
import HeroStatUpdateWrapper from './HeroStatUpdateWrapper'
import HeroGameView from './View/HeroGameView'

interface IHeroWrapperProps {}

const HeroWrapper: React.FunctionComponent<IHeroWrapperProps> = (props) => {
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const {
    heroLevel,
    heroXp,
    setHeroLevel,
    setHeroXp,
    heroSkillPoints,
    setHeroSkillPoints,
    setHeroMaxHp,
    heroMaxHp,
    heroHp,
    setHeroHp
  } = useContext(HeroContext)

  useEffect(() => {
    if (
      heroXp >= xpToNextLevel(heroLevel) &&
      heroXp > 0 &&
      xpToNextLevel(heroLevel) > 0
    ) {
      const diff = Math.abs(heroXp - xpToNextLevel(heroLevel))
      sendMessage('hero', { levelUp: true })
      sendMessage('hero', { levelUp: false }, 1000)
      setHeroSkillPoints(heroSkillPoints + calculateSkillPoints(heroLevel + 1))
      setHeroLevel(heroLevel + 1)
      const newMaxHp = calculateHpOnLevelUp(heroMaxHp)
      if (heroHp === heroMaxHp) {
        setHeroHp(newMaxHp)
      }
      setHeroMaxHp(newMaxHp)
      setHeroXp(diff)
      setActionLogs([
        ...actionLogs,
        formatNewLog(
          EActionLogType.playerAction,
          `Player level up, now level ${heroLevel + 1}`
        )
      ])
    }
  }, [
    actionLogs,
    heroHp,
    heroLevel,
    heroMaxHp,
    heroSkillPoints,
    heroXp,
    setActionLogs,
    setHeroHp,
    setHeroLevel,
    setHeroMaxHp,
    setHeroSkillPoints,
    setHeroXp
  ])

  return (
    <div>
      <HeroStatUpdateWrapper />
      <HeroGameView />
    </div>
  )
}

export default HeroWrapper
