import * as React from 'react'
import { useContext, useEffect } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import {
  ActionLogsContext,
  EnemiesContext,
  GameContext
} from '../utils/context'
import { EActionLogType, EEnemyState, ERoomState } from '../utils/interfaces'

interface IUpdateRoomStateProps {}

const UpdateRoomState: React.FunctionComponent<IUpdateRoomStateProps> = (
  props
) => {
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const { enemies } = useContext(EnemiesContext)
  const { roomState, setRoomState } = useContext(GameContext)
  useEffect(() => {
    const hasEnemyAlive = enemies.some(
      (enemy) => enemy.enemyState === EEnemyState.alive
    )

    if (roomState !== ERoomState.finished && !hasEnemyAlive) {
      setRoomState(ERoomState.finished)
      setActionLogs([
        ...actionLogs,
        formatNewLog(EActionLogType.system, 'Room is finished')
      ])
    }
  }, [actionLogs, enemies, roomState, setActionLogs, setRoomState])

  return null
}

export default UpdateRoomState
