import { EEntity, IEnemy, IEntityTurns } from '../utils/interfaces'

export const generateGameTurn = (
  hero: { heroEntity: EEntity },
  enemies: IEnemy[]
): IEntityTurns => {
  const flow: IEntityTurns = []
  flow[0] = { id: -1, entity: EEntity.player }
  enemies.forEach((enemy: IEnemy, idx: number) => {
    flow[idx + 1] = { id: enemy.enemyId, entity: enemy.enemyEntity }
  })
  return flow
}
