import classNames from 'classnames'
import * as React from 'react'
import { useContext, useEffect, useRef } from 'react'
import { ActionLogsContext } from '../utils/context'
import { EActionLogType, IActionLog } from '../utils/interfaces'

interface IActionLogPanelProps {}

const ActionLogPanel: React.FunctionComponent<IActionLogPanelProps> = (
  props
) => {
  const { actionLogs } = useContext(ActionLogsContext)
  const logRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (logRef && logRef.current) {
      logRef.current.scrollTop = logRef.current.scrollHeight
    }
  }, [actionLogs])

  const formattedTime = (timestamp: string) => {
    const date = new Date(timestamp)
    return (
      date.getHours() +
      ':' +
      date.getMinutes() +
      ':' +
      ('0' + date.getSeconds()).slice(-2)
    )
  }
  return (
    <div className="mt-10">
      <div>Logs</div>
      <div
        className="max-h-48 h-48 overflow-scroll border border-black bg-white px-4 py-2"
        ref={logRef}
      >
        <div className="">
          {actionLogs.map((actionLog: IActionLog, idx: number) => {
            return (
              <div key={idx} className="flex">
                <div>{formattedTime(actionLog.timestamp) + ' : '}</div>
                <div
                  className={classNames({
                    'text-gray-400 italic':
                      actionLog.type === EActionLogType.system,
                    'text-red-500':
                      actionLog.type === EActionLogType.enemyAction,
                    'text-blue-500':
                      actionLog.type === EActionLogType.playerAction
                  })}
                >
                  {actionLog.message}
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  )
}

export default ActionLogPanel
