import { enemiesResource, itemsResource } from '../Resources/GameResource'
import {
  ENEMY_BASE_HEALTH,
  MAX_ENEMIES_TYPE,
  MAX_ENEMY,
  MAX_LOOT
} from './constants'
import { EEnemyState, EEntity, EItemType, IEnemy, IITems } from './interfaces'
import { calculateHp, rand, randMinMax } from './utils'

/**
 * Generate the room, how many enemies
 */
export const enemiesGenerator = (heroLevel: number) => {
  const maxEnemy = rand(MAX_ENEMY) + 1
  let enemies: IEnemy[] = []

  for (let i = 0; i < maxEnemy; i++) {
    enemies.push(enemyGenerator(i, heroLevel))
  }
  return enemies
}

/**
 * Generate each enemy individualy, level, base dmg, hp, weapon
 */
export const enemyGenerator = (id: number, heroLevel: number): IEnemy => {
  const enemyLevel = Math.round(randMinMax(heroLevel, heroLevel + 2))
  const enemyType = rand(MAX_ENEMIES_TYPE)
  const enemyStats = {
    strength: Math.round(randMinMax(enemyLevel, enemyLevel * 3)),
    vitality: Math.round(randMinMax(enemyLevel, enemyLevel * 3)),
    dexterity: Math.round(randMinMax(enemyLevel, enemyLevel * 3)),
    robustness: Math.round(randMinMax(enemyLevel, enemyLevel * 3))
  }
  const maxHp = calculateHp(
    enemyLevel,
    enemyStats.vitality,
    enemiesResource[enemyType].base_hp,
    ENEMY_BASE_HEALTH
  )
  return {
    enemyId: id,
    enemyType: enemyType,
    enemyLevel: enemyLevel,
    enemyHp: maxHp,
    enemyMaxHp: maxHp,
    enemyEntity: EEntity.foe,
    enemyState: EEnemyState.alive,
    enemyLoots: lootGenerator(MAX_LOOT),
    enemyStats
  } as IEnemy
}

