import classNames from 'classnames'
import * as React from 'react'
import { useContext } from 'react'
import { actionsResource } from '../../Resources/GameResource'
import { GameContext, PlayerStateContext } from '../../utils/context'
import { EEntity, IPlayerAction } from '../../utils/interfaces'

interface IActionViewProps {
  actionOnClick: (action: IPlayerAction) => void
}

const ActionView: React.FunctionComponent<IActionViewProps> = ({
  actionOnClick
}) => {
  const { entityTurn } = useContext(GameContext)
  const { isWaitingForEnemySelection } = useContext(PlayerStateContext)

  return (
    <div className="flex">
      {Object.keys(actionsResource).map((key: string, idx: number) => {
        return (
          <div className="mr-4" key={idx}>
            <button
              className={classNames(
                'border border-gray-600 bg-white px-4 mt-4 active:bg-gray-200 shadow disabled:text-gray-400 disabled:border-gray-400',
                {
                  'bg-gray-400': isWaitingForEnemySelection
                }
              )}
              type="button"
              onClick={() => actionOnClick(actionsResource[key].type)}
              disabled={entityTurn.entity !== EEntity.player}
            >
              {actionsResource[key].label}
            </button>
          </div>
        )
      })}
    </div>
  )
}

export default ActionView
