import * as React from 'react'
import { useContext } from 'react'
import { enemiesGenerator } from '../utils/roomGenerator'
import { generateGameTurn } from '../utils/turnBasedGenerator'
import { formatNewLog } from '../Logs/actionLogsUtils'
import {
  ActionLogsContext,
  EnemiesContext,
  GameContext,
  HeroContext
} from '../utils/context'
import { EActionLogType, ERoomState, IEntityTurns } from '../utils/interfaces'
import RoomEndingView from './View/RoomEndingView'

interface IRoomEndingProps {}

const RoomEnding: React.FunctionComponent<IRoomEndingProps> = (props) => {
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const { setEnemies } = useContext(EnemiesContext)
  const { heroEntity, heroLevel } = useContext(HeroContext)
  const {
    setEntityTurn,
    setEntityTurns,
    roomNumber,
    setRoomNumber,
    setRoomState
  } = useContext(GameContext)

  const handleOnClick = () => {
    setRoomState(ERoomState.onGoing)
    const enemies = enemiesGenerator(heroLevel)
    setEnemies(enemies)
    const turn: IEntityTurns = generateGameTurn({ heroEntity }, enemies)
    setEntityTurns(turn)
    setEntityTurn(turn[0])
    setActionLogs([
      ...actionLogs,
      formatNewLog(EActionLogType.system, 'New room is loaded with new enemies')
    ])
    setRoomNumber(roomNumber + 1)
  }

  return <RoomEndingView handleOnClick={handleOnClick} />
}

export default RoomEnding
