import * as React from 'react'
import { useContext, useState } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import { MAX_HERO_HEAD_STYLE_TYPE } from '../utils/constants'
import { ActionLogsContext, GameContext, HeroContext } from '../utils/context'
import { EActionLogType, EGameState, ERoomState } from '../utils/interfaces'
import { rand } from '../utils/utils'

interface IStartScreenProps {}

const StartScreen: React.FunctionComponent<IStartScreenProps> = (props) => {
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const { setHeroName, setHeroHeadId, setHeroSkillPoints } =
    useContext(HeroContext)
  const { setGState, setRoomState } = useContext(GameContext)
  const [inputText, setInputText] = useState<string>('')

  const handleOnClick = (e: any) => {
    e.preventDefault()
    e.stopPropagation()

    setHeroName(inputText)
    setHeroHeadId(rand(MAX_HERO_HEAD_STYLE_TYPE))
    setHeroSkillPoints(0)
    setGState(EGameState.notInitialised)
    setRoomState(ERoomState.readyToStart)
    const newLogs = [
      ...actionLogs,
      formatNewLog(EActionLogType.system, 'Player name is set'),
      formatNewLog(EActionLogType.system, 'Game state is "Not Initialised"')
    ]
    setActionLogs(newLogs)
  }

  return (
    <div className="flex justify-center mt-24">
      <div>
        <div className="text-5xl text-center mb-12">Text RPG</div>
        <div>
          <div className="text-center">Enter your hero's name</div>
          <div className="mt-4 flex justify-center">
            <form id="start-input">
              <input
                className="px-2"
                onChange={(e) => setInputText(e.target.value)}
                value={inputText}
                placeholder="Ligma"
              />
            </form>
          </div>
          <div className="flex justify-center">
            <button
              className="border border-gray-600 bg-white px-4 mt-4 active:bg-gray-200 shadow disabled:text-gray-400 disabled:border-gray-400"
              type="submit"
              form="start-input"
              onClick={handleOnClick}
              disabled={inputText.length === 0}
            >
              Let's go
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default StartScreen
