import * as React from 'react'
import { useContext } from 'react'
import { GameContext, HeroContext } from '../../utils/context'
import { xpToNextLevel } from '../../utils/utils'

interface IHeroInfoViewProps {}

const HeroInfoView: React.FunctionComponent<IHeroInfoViewProps> = (props) => {
  const { roomNumber } = useContext(GameContext)
  const { heroName, heroLevel, heroHp, heroMaxHp, heroXp, heroSkillPoints } =
    useContext(HeroContext)

  return (
    <div>
      <div>Name: {heroName}</div>
      <div>Level: {heroLevel}</div>
      <div>HP: {`${heroHp} / ${heroMaxHp}`}</div>
      <div>
        XP: {heroXp} / {xpToNextLevel(heroLevel)}
      </div>
      <div>Skill points: {heroSkillPoints}</div>
      <div>{`Room # ${roomNumber}`}</div>
    </div>
  )
}

export default HeroInfoView
