import classNames from 'classnames'
import * as React from 'react'
import { useContext } from 'react'
import { HeroContext } from '../../utils/context'
import { IStats } from '../../utils/interfaces'
import {
  calculateDefenseMitigation,
  calculateDodgeChance,
  calculateExtraHpFromVitality,
  getBaseDmg
} from '../../utils/utils'

interface IHeroStatsViewProps {
  handleStatUpdate: (stat: keyof IStats, value: number) => void
}

export const showStatData = (
  level: number,
  stat: keyof IStats,
  stats: IStats
) => {
  switch (stat) {
    case 'strength':
      return (
        <div className="flex-1 ml-3 italic">{`(Base dmg: ${getBaseDmg(
          level,
          stats[stat]
        )})`}</div>
      )
    case 'vitality':
      return (
        <div className="flex-1 ml-3 italic">{`(${calculateExtraHpFromVitality(
          level,
          stats[stat] + 1
        )} hp per pt)`}</div>
      )
    case 'dexterity':
      return (
        <div className="flex-1 ml-3 italic">{`(${
          Math.round(calculateDodgeChance(stats[stat]) * 100) / 100
        } % evade)`}</div>
      )
    case 'robustness':
      return (
        <div className="flex-1 ml-3 italic  min-w-[112px]">{`(${
          Math.round(calculateDefenseMitigation(stats[stat]) * 100) / 100
        } % dmg reduction)`}</div>
      )
    default:
      return <div className="flex-1 ml-3"></div>
  }
}

const HeroStatsView: React.FunctionComponent<IHeroStatsViewProps> = ({
  handleStatUpdate
}) => {
  const { heroStats, heroSkillPoints, heroLevel } = useContext(HeroContext)

  const hasPoints = () => heroSkillPoints > 0

  return (
    <div className="bg-white border border-black p-4 ml-10 self-center">
      {Object.keys(heroStats).map((key: string, idx: number) => (
        <div className="flex h-8" key={idx}>
          <div className="flex">
            <div className="min-w-[125px]">{`${key as string} : ${
              heroStats[key as keyof IStats]
            }`}</div>
            {hasPoints() && (
              <div className="ml-4">
                <button
                  className={classNames(
                    'border border-gray-600 bg-white active:bg-gray-200 shadow w-7 disabled:text-gray-400 disabled:border-gray-400'
                  )}
                  type="button"
                  onClick={() => handleStatUpdate(key as keyof IStats, 1)}
                >
                  +
                </button>
              </div>
            )}
          </div>
          {showStatData(heroLevel, key as keyof IStats, heroStats)}
        </div>
      ))}
    </div>
  )
}

export default HeroStatsView
