import * as React from 'react'
import { useCallback, useContext, useEffect } from 'react'
import {
  ActionLogsContext,
  EnemiesContext,
  GameContext,
  HeroContext,
  PlayerStateContext
} from '../utils/context'
import { EPlayerAction } from '../utils/interfaces'
import { handleHeroAtk } from './actionUtils'
import ActionView from './View/ActionsView'

interface IActionWrapperProps {}

const ActionWrapper: React.FunctionComponent<IActionWrapperProps> = (props) => {
  const { heroLevel, heroXp, setHeroXp, heroStats } = useContext(HeroContext)
  const { enemies, setEnemies } = useContext(EnemiesContext)
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)
  const { entityTurns, setEntityTurn, setEntityTurns } = useContext(GameContext)
  const {
    setIsWaitingForEnemySelection,
    setPlayerAction,
    enemySelected,
    setEnemySelected
  } = useContext(PlayerStateContext)

  const setNextTurn = useCallback(() => {
    entityTurns.shift()
    setEntityTurns([...entityTurns])
    setEntityTurn(entityTurns[0])
  }, [entityTurns, setEntityTurn, setEntityTurns])

  const handleActionClick = (type: EPlayerAction) => {
    if (type === EPlayerAction.attack) {
      setIsWaitingForEnemySelection(true)
      setPlayerAction(EPlayerAction.attack)
    }
  }

  useEffect(() => {
    if (enemySelected) {
      handleHeroAtk(
        enemySelected,
        heroLevel,
        heroXp,
        setHeroXp,
        enemies,
        setEnemies,
        actionLogs,
        setActionLogs,
        heroStats
      )
      setNextTurn()
      setEnemySelected(undefined)
      setIsWaitingForEnemySelection(false)
    }
  }, [
    actionLogs,
    enemies,
    enemySelected,
    heroLevel,
    heroStats,
    heroXp,
    setActionLogs,
    setEnemies,
    setEnemySelected,
    setHeroXp,
    setIsWaitingForEnemySelection,
    setNextTurn
  ])

  return <ActionView actionOnClick={handleActionClick} />
}

export default ActionWrapper
