import { formatNewLog } from '../Logs/actionLogsUtils'
import { replaceEnemyByEnemy } from '../Enemy/enemyUtils'
import { enemiesResource } from '../Resources/GameResource'
import {
  IActionLog,
  EActionLogType,
  EEnemyState,
  IEnemy,
  IStats
} from '../utils/interfaces'
import {
  calculateDefenseMitigation,
  calculateDodgeChance,
  calculateXpDrop,
  compareEnemyId,
  getDamage,
  isEvading,
  sendMessage
} from '../utils/utils'

export const handleHeroAtk = (
  enemySelected: IEnemy,
  heroLevel: number,
  heroXp: number,
  setHeroXp: (arg: number) => void,
  enemies: IEnemy[],
  setEnemies: (arg: IEnemy[]) => void,
  actionLogs: IActionLog[],
  setActionLogs: (arg: IActionLog[]) => void,
  heroStats: IStats
) => {
  let newLogs: IActionLog[] = []
  sendMessage('hero', { atk: true })
  sendMessage('hero', { atk: false }, 1000)

  const evading = isEvading(
    calculateDodgeChance(enemySelected.enemyStats.dexterity) / 100
  )
  if (!evading) {
    const dmg = getDamage(heroLevel, heroStats.strength)
    const dmgReduction = calculateDefenseMitigation(
      enemySelected.enemyStats.robustness
    )
    const mitigatedDmg = Math.round(dmg * (dmgReduction / 100))
    const updatedDmg = dmg - mitigatedDmg
    enemySelected.enemyHp -= updatedDmg
    newLogs.push(
      formatNewLog(
        EActionLogType.playerAction,
        `Player is attacking ${
          enemiesResource[enemySelected.enemyType].name
        } is attacking player for ${updatedDmg} damage (mitigated ${mitigatedDmg} damage)`
      )
    )

    if (enemySelected.enemyHp <= 0) {
      enemySelected.enemyHp = 0
      enemySelected.enemyState = EEnemyState.dead
      const xp = calculateXpDrop(
        enemySelected.enemyLevel,
        enemiesResource[enemySelected.enemyType].base_xp
      )
      setHeroXp(heroXp + xp)
      newLogs.push(
        formatNewLog(
          EActionLogType.enemyAction,
          `${enemiesResource[enemySelected.enemyType].name} is dead`
        ),
        formatNewLog(
          EActionLogType.playerAction,
          `Player gain ${xp} xp for killing ${
            enemiesResource[enemySelected.enemyType].name
          }`
        )
        // formatNewLog(
        //   EActionLogType.enemyAction,
        //   `${enemiesResource[enemySelected.enemyType].name} dropped ${
        //     enemySelected.enemyLoots[0].amount
        //   } ${itemsResource[enemySelected.enemyLoots[0].type].label}`
        // )
      )
    }
    setEnemies(replaceEnemyByEnemy(enemies, enemySelected).sort(compareEnemyId))
  } else {
    sendMessage('enemy', { id: enemySelected.enemyId, evade: true })
    sendMessage('enemy', { id: enemySelected.enemyId, evade: false }, 1000)
    newLogs.push(
      formatNewLog(
        EActionLogType.playerAction,
        `Player is attacking ${enemiesResource[enemySelected.enemyType].name}`
      )
    )
    newLogs.push(
      formatNewLog(
        EActionLogType.playerAction,
        `${enemiesResource[enemySelected.enemyType].name} just evade the attack`
      )
    )
  }
  setActionLogs([...actionLogs, ...newLogs])
}
