import * as React from 'react'
import { useContext } from 'react'
import { formatNewLog } from '../Logs/actionLogsUtils'
import { ActionLogsContext, GameContext, HeroContext } from '../utils/context'
import { defaultHero } from '../utils/defaultData'
import { EGameState, EActionLogType } from '../utils/interfaces'
import DeathScreenView from './View/DeathScreenView'

interface IDeathScreenWrapperProps {}

const DeathScreenWrapper: React.FunctionComponent<IDeathScreenWrapperProps> = (
  props
) => {
  const {
    setHeroHp,
    setHeroLevel,
    setHeroXp,
    setHeroItems,
    setHeroSkillPoints,
    setHeroStats,
    heroStats,
    setHeroMaxHp
  } = useContext(HeroContext)
  const { setGState, setRoomNumber } = useContext(GameContext)
  const { actionLogs, setActionLogs } = useContext(ActionLogsContext)

  const handleOnClick = (e: any) => {
    e.preventDefault()
    e.stopPropagation()

    setHeroHp(defaultHero.heroHp)
    setGState(EGameState.notInitialised)
    const newLogs = [
      ...actionLogs,
      formatNewLog(EActionLogType.system, 'Game is restarting'),
      formatNewLog(EActionLogType.system, 'Game state is "Not Initialised"')
    ]
    setActionLogs(newLogs)
    setHeroXp(0)
    setHeroSkillPoints(0)
    heroStats.strength = 0
    heroStats.vitality = 0
    heroStats.dexterity = 0
    heroStats.robustness = 0
    setHeroStats({ ...heroStats })
    setHeroLevel(1)
    setHeroHp(100)
    setHeroMaxHp(100)
    setRoomNumber(1)
  }

  return <DeathScreenView handleOnClick={handleOnClick} />
}

export default DeathScreenWrapper
