import * as React from 'react'
import { useContext } from 'react'
import { HeroContext } from '../utils/context'
import { IStats } from '../utils/interfaces'
import { sendMessage } from '../utils/utils'
import HeroInfoView from './View/HeroInfoView'
import HeroStatsView from './View/HeroStatsView'

interface IHeroInfoWrapperProps {}

const HeroInfoWrapper: React.FunctionComponent<IHeroInfoWrapperProps> = (
  props
) => {
  const { heroStats, setHeroStats, heroSkillPoints, setHeroSkillPoints } =
    useContext(HeroContext)

  const handleStatUpdate = (stat: keyof IStats, value: number) => {
    heroStats[stat] = heroStats[stat] + value
    setHeroStats({ ...heroStats })
    setHeroSkillPoints(heroSkillPoints - 1)
    sendMessage('stat', { [stat]: heroStats[stat] })
  }
  return (
    <div className="flex ">
      <div className="min-w-[150px] bg-white border border-black p-4">
        <HeroInfoView />
      </div>
      <HeroStatsView handleStatUpdate={handleStatUpdate} />
    </div>
  )
}

export default HeroInfoWrapper
