import { Dialog } from '@headlessui/react'
import * as React from 'react'
import { useContext, useEffect, useState } from 'react'
import { EnemiesContext, GameContext, HeroContext } from '../utils/context'
import { ERoomState, IITems } from '../utils/interfaces'
import { mergeItemsArray } from '../utils/utils'

interface IPopupRoomLootProps {}

const PopupRoomLoot: React.FunctionComponent<IPopupRoomLootProps> = (props) => {
  const [isOpen, setIsOpen] = useState<boolean>(false)
  const [roomLoot, setRoomLoot] = useState<IITems[]>([])
  const { enemies } = useContext(EnemiesContext)
  const { roomState } = useContext(GameContext)
  const { heroItems, setHeroItems } = useContext(HeroContext)

  useEffect(() => {
    if (!isOpen && roomState === ERoomState.finished && roomLoot.length === 0) {
      const loots: IITems[] = []
      enemies.forEach((enemy) => {
        loots.push(...enemy.enemyLoots)
      })
      setRoomLoot(mergeItemsArray(loots))
      setIsOpen(true)
    }
  }, [roomState, enemies, isOpen, roomLoot.length, roomLoot])

  useEffect(() => {
    if (roomLoot.length > 0 && roomState !== ERoomState.finished) {
      setRoomLoot([])
    }
  }, [roomLoot.length, roomState])

  const handleOnClick = () => {
    const allLoots = mergeItemsArray([...heroItems, ...roomLoot])
    setHeroItems(allLoots)
    setIsOpen(false)
  }

  return (
    <div>
      <Dialog
        open={isOpen}
        onClose={() => setIsOpen(false)}
        className="fixed z-10 inset-0 overflow-y-auto"
      >
        <div className="flex items-center justify-center min-h-screen p-10">
          <Dialog.Overlay className="fixed inset-0 bg-black opacity-50" />

          <div className="relative bg-white rounded mx-auto p-6">
            <Dialog.Title className="text-xl font-semibold">
              GG here are your loots
            </Dialog.Title>
            <div className="mt-6">
              {roomLoot.length > 0 ? (
                roomLoot.map((loot: IITems, idx: number) => (
                  <div key={idx}>
                    {loot.itemResource.label} x {loot.amount}
                  </div>
                ))
              ) : (
                <div>Sorry, there wasn't any loot this round...</div>
              )}
              <div className="flex justify-center">
                <button
                  className="border border-gray-600 bg-white px-4 mt-4 active:bg-gray-200 shadow disabled:text-gray-400 disabled:border-gray-400"
                  type="button"
                  onClick={() => {
                    handleOnClick()
                    setIsOpen(false)
                  }}
                >
                  {roomLoot.length > 0
                    ? 'Gimme the loot and dismiss'
                    : 'Dismiss'}
                </button>
              </div>
            </div>
          </div>
        </div>
      </Dialog>
    </div>
  )
}

export default PopupRoomLoot
